import React from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { Field, reduxForm } from 'redux-form';
import { Redirect } from 'react-router-dom';
import { v4 as uuidv4 } from 'uuid';
import { register } from '../../redux/auth-reducer';
import { addUser } from '../../redux/users-reducer'
import { required } from '../../utils/validators';
import { Input } from '../common/FormControl';
import { getRandomDefaultPhoto } from '../../utils/helpers';

const RegistrationForm = ( {handleSubmit, error} ) => {
    return(
        <form onSubmit={handleSubmit}>
            <div>
                <Field placeholder='' component={Input} type='email' name='email' validate={[required]}/>
            </div>
            <div>
                <Field placeholder="` " component={Input} name='userName' validate={[required]}/>
            </div>
            <div>
                <Field placeholder='' component={Input} type='password' name='password' validate={[required]}/>
            </div>
            <div>
                <Field placeholder=' ' component={Input} type='password' name='confirmPassword' validate={[required]}/>
            </div>
            <div>
                <Field placeholder='' component={Input} name='status' validate={[required]}/>
            </div>
            <div>
                <Field placeholder='' component={Input} name='country' validate={[required]}/>
                <Field placeholder='' component={Input} name='city' validate={[required]}/>
            </div>
            { error && <div className='error-border'>
                { error }
            </div> }
            <button></button>
            <p className='or'></p>
            <div className='redirect-to-registration'>
                <NavLink to='/login'>  </NavLink>
            </div>
        </form>
    )
}

const RegistrationReduxForm = reduxForm({form: 'registration'})(RegistrationForm);

const Registration = ({isAuth, register, addUser}) => {
    let errorMessage = '';
    const sendRegistrationData = (formData) => {
        let user = {
            id: uuidv4(),
            email: formData.email,
            defaultPhoto: getRandomDefaultPhoto(),
            photo: '',
            password: formData.password,
            userName: formData.userName,
            status: formData.status,
            location: {country: formData.country, city: formData.city},
            networks: [],
            following: [],
            posts: [],
            messages: []
        }
        if(formData.email && formData.password && formData.userName && formData.status && formData.country && formData.city && formData.password===formData.confirmPassword) {
            addUser(user);
            register(user);
        } else if(formData.password!==formData.confirmPassword) {
            errorMessage = '  '
        } else {
            errorMessage = '  '
        }
    }
    if(isAuth) {
        return <Redirect to='/profile' />
    }
    return(
        <div className='login'>
            <h1 className='login-logo'></h1>
            <RegistrationReduxForm onSubmit={sendRegistrationData}/>
            {errorMessage && <div className='error-message'>{errorMessage}</div>}
        </div>
    )
}

const mapStateToProps = (state) => ({
    isAuth: state.authReducer.isAuth
})

export default connect( mapStateToProps, {register, addUser})(Registration);