import React, { useState } from 'react';
import { connect } from 'react-redux';
import { NavLink } from 'react-router-dom';
import { logout } from '../../redux/auth-reducer';
import catLogo from '../../assets/img/helpers/catLogo.png'
import './Header.scss'

const Header = (props) => {
    return <header className='header'>
        <div className='cap'>
            <div className='logo'><h1>.ua</h1></div>
            <div className='login'>
                {
                    props.isAuth 
                    ? <div className='user-email'>{props.user.email} <a onClick={props.logout} className='auth-button'></a> </div>
                    : <div><NavLink to='/login' className='auth-button'>  </NavLink> <NavLink to='/registration' className='auth-button'>  </NavLink></div>
                }
            </div>
        </div>
        <nav className='navbar'>
            <div>
                <NavLink to='/profile'></NavLink>
            </div>
            <div>
                <NavLink to='/news'></NavLink>
            </div>
            <div>
                <NavLink to='/users'> </NavLink>
            </div>
            <div className='space'><img src={catLogo} /></div>
            <div>
                <NavLink to='/friends'></NavLink>
            </div>
            <div>
                <NavLink to='/messages'></NavLink>
            </div>
            <div>
                <NavLink to='/settings'></NavLink>
            </div>
        </nav>
    </header>
}

export default Header;